body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
nav {
    display: flex;
    justify-content: space-between;
    background-color: #1d809f;
    align-items: center;
    padding-right: 20px;
    padding-left: 20px;



}

.logo {
    color: #ffffff;
    font-size: 30px;
}

nav ul {
    list-style: none;
}

nav ul li {
    display: inline-block;
    margin-right: 8px;

}

nav ul li a {
    text-decoration: none;
    color: #ffffff;
}

img {
    display: 100%
}
